.menu-item {
  width: 20rem;
  height: 15rem;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  margin: 0 15px 15px;
  overflow: hidden;
  margin-left: 0;

  &:hover {
    cursor: pointer;
    & .background-image {
      transform: scale(1.1);
      transition: 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }
    .content {
      opacity: .8;
    }
  }


  &.large {
    height: 22rem;
    width: 25rem;
  }

  &:nth-child(3) {
    margin-right: 0;
  }


  &:last-child {
    margin-right: 0;
  }
  
  .background-image {
    width: 100%;
    // height: 100%;
  }

  .content {
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    background-color: #fff;
    opacity: 0.6;
    position: absolute;

    .title {
      font-weight: bold;
      margin-bottom: 6px;
      font-size: 22px;
      color: #4a4a4a;
    }

    .subtitle {
      font-weight: lighter;
      font-size: 16px;
    }
  }
}
