.collection-item {
	width: 15rem;
	overflow: hidden;
	position: relative;
	transition: .5s;
	margin: 0.3rem 0.3rem;
	.custom-button {
		display: none;
		width: 80%;
		opacity: 0.7;
		position: absolute;
		bottom: 3rem;
		left: 50%;
		transform: translateX(-50%);
		cursor: pointer;
	}

	&:hover {
		opacity: 0.8;
		.custom-button {
			display: flex;
			opacity: 0.85;
		}
	}

	img {
		max-height: 355px;
	}

	button {
		cursor: pointer;
	}
}



.collection-footer {
	display: flex;
	justify-content: space-between;
	padding: 0 0.5rem;
}
