$sub-color: grey;
$main-color: black;


.group {
  position: relative;
  margin: 45px 0;


  .form-input {
    background: none;
    background-color: white;
    color: $sub-color;
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid $sub-color;
    margin: 25px 0;
    &:focus {
      outline: none;
	  color: $main-color;
    }

  }

  input[type='password'] {
    letter-spacing: 0.1em;
  }

  .form-input-label {
    color: $sub-color;
    font-size: 16px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 10px;
    transition: 300ms ease all;

  }
}
